.App {
  text-align: center;
  min-height: 100vh;
}

.create-story-form {
  margin: 5em auto 5em auto;
  display: flex;
  flex-direction: column;
  width: 50vw;

  & > input {
    margin-top: 1em;
    margin-bottom: 1em;
  }

  & > textarea {
    margin-top: 1em;
    margin-bottom: 1em;
  }

  & > select {
    margin: 1em auto
  }

}

.login-form {
  margin: 5em auto 5em auto;
  display: flex;
  flex-direction: column;
  width: 50vw;

  & > input {
    margin: 1em auto
  }

  & > button {
    margin: 1em auto
  }
}

.story-container {
  margin: 1em auto;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

section {
  display: table;
  width: 100%;
}

section > * {
  display: table-row;
}

section .col {
  display: table-cell;
  padding: 1em;
}

.btn {
  padding: 1em;
  margin: .5em;
  color: #fff;
  &-accept {
    background: #008000;
  }
  &-reject {
    background: #ff0000;
  }
}

.text-white {
  color: #fff;
}

.bg-black {
  background: #000;
}

.bg-red {
  background: #ff0000;
}

.bg-green {
  background: #008000;
}